* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}

body, html {
    background: #151515;
    /* scroll-snap-type: y proximity; */
}

#landing {
    background: url('./Assets/background.svg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    scroll-snap-align: start;
}

#header {
    position: absolute;
    top: 0px;
    left: 0px;
    display: flex;
    justify-content: space-between;
    padding: 12px 24px;
    width: calc(100% - 48px);
}

.headertext {
    font-size: 24px;
    color: #ffffffc6;
    font-weight: 300;
    padding: 4px 8px;
}

.headerbutton:hover {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    transition: background 0.25s;
    cursor: pointer;
}

#name {
    font-size: 102px;
    /* color: #ffffff74; */
    color: #ffffff;
    font-weight: lighter;
    font-stretch: expanded;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.803) 20%, rgba(255, 255, 255, 0.201) 40%, rgba(255, 255, 255, 0.201) 60%, rgba(255, 255, 255, 0.803) 80%);
    background-size: 200% auto;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: fading 7s linear infinite;
}

.custom-shape-divider-bottom-1680923267 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-bottom-1680923267 svg {
    position: relative;
    display: block;
    width: calc(149% + 1.3px);
    height: 100px;
}

.custom-shape-divider-bottom-1680923267 .shape-fill {
    fill: #151515;
}

@keyframes fading {
    to {
        background-position: 200% center;
    }
}

