.section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
    /* background: green; */
    position: relative;
    /* background: url('./mainbackground.svg');
	background-size: 400% 400%;
	animation: gradient 30s ease infinite; */
    scroll-snap-align: start;
}
