.sectiontitle {
    font-size: 72px;
    color: #ffffff;
    font-weight: lighter;
    font-stretch: expanded;
    /* position: absolute;
    top: 10%;
    transform: translateY(-50%); */
    height: 20%;
    display: flex;
    align-self: center;
}

.collection {
    height: 60%;
    width: 100%;
    display: flex;
    gap: 0 60px;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    align-items: center;
    /* background: red; */
}

.job {
    height: 100px;
    width: 475px;
    display: flex;
    justify-content: left;
    align-items: center;
    position: relative;
    transition: all 0.25s;
    z-index: 1;
}

.jobinsides {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 8px;
    position: relative;
    z-index: 1;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba(138, 138, 138, 0.1);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding-right: 4px;
}

.projectinsides {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 8px;
    position: relative;
    z-index: 1;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba(138, 138, 138, 0.1);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding-right: 16px;
}

.icon {
    min-height: 50px;
    min-width: 50px;
    max-height: 50px;
    max-width: 50px;
    margin: 0 10px;

    filter: drop-shadow(3px 3px 2px rgb(0 0 0 / 0.2));
}

.linkicon {
    min-height: 35px;
    min-width: 35px;
    max-height: 35px;
    max-width: 35px;
    margin: 0 10px;
    padding: 8px;
    border-radius: 12px;

    background:rgb(67, 67, 67);
    box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.2);
}

.subtitle {
    font-size: 24px;
    color: #ffffff;
    font-weight: 300;
}

.desc {
    font-size: 18px;
    color: #cbcbcb;
    font-weight: 300;
}

.infoicon {
    position: absolute;
    top: 8px;
    right: 8px;
    height: 24px;
    width: 24px;
    transition: all 0.25s;
    padding: 4px;
}

.infoicon:hover {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 6px;
    cursor: pointer;
}

.jobinfo {
    justify-content: center;
    align-items: center;
    position: absolute;
    transition: all 0.25s;
    height: 76px;
    width: 451px;
    top: 0px;
    left: 0px;
    z-index: 1;
    padding: 12px;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba(138, 138, 138, 0.1);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.jobinfotext {
    font-size: 18px;
    color: #cbcbcb;
    font-weight: 300;
    margin-right: 24px;
}

.closeicon {
    position: absolute;
    top: 8px;
    right: 8px;
    height: 24px;
    width: 24px;
    transition: all 0.25s;
    padding: 4px;
}

.closeicon:hover {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 6px;
    cursor: pointer;
}

#aboutcontent {
    height: 60%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border-top: 1px solid #a2a2a2; */
}

.abouttext {
    font-size: 20px;
    color: #ffffff;
    font-weight: 300;
    text-align: center;
    max-width: 70%;
    margin-bottom: 48px;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba(138, 138, 138, 0.1);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding: 18px 32px;
}

#contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 24px;
}

.contact {
    height: 100px;
    width: 475px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 0.25s;
    cursor: pointer;
}

.contactcontent {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 8px;
    position: relative;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba(138, 138, 138, 0.1);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding: 0 12px;
}

.contactinfo {
    font-size: 24px;
    color: #ffffff;
    font-weight: 300;
}